@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Roboto+Mono:wght@700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100vw;
  font-family: "Roboto Mono", monospace;
  background-color: #111726;
  overflow-x: hidden;
  background-image: url(/static/media/noise.d9fbba13.png);
  background-size: 13%;
}

.login {
  color: rgb(255, 255, 255);
  display: grid;
  place-items: center;
  height: 100vh;
}
.loginContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -50px;
}
.loginContainer img {
  margin-bottom: 10px;
}
.loginForm {
  display: flex;
  flex-direction: column;
  background-color: transparent;
  padding: 20px 20px;
  border-radius: 20px;
  font-size: large;
  max-width: 400px;
  align-items: center;
}

.loginContainer__input {
  font-size: 18px;
  padding: 10px 5px;
  outline: none;
  border: 1px solid #39e8b0;
  border-radius: 5px;
  margin-top: 20px;
  color: white;
  background: transparent;
  outline: none;
}
.loginContainer__input[type="text"]:focus {
  outline: none;
  border: 1px solid #39e8b0;
  box-shadow: 0px 0px 14px #39e8b157;
  -moz-box-shadow: 0px 0px 14px #39e8b157;
  -webkit-box-shadow: 0px 0px 14px #39e8b157;
  transition: 200ms ease-in;
}

.loginContainer_bottom {
  margin-top: 15px;
  font-size: small;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.loginContainer_bottom p {
  margin-top: 10px;
}
.signinButton {
  border-radius: 5px;
  padding: 10px 10px;
  margin-top: 15px;
  outline: none;
  border: none;
  background-color: #39e8b0;
  color: #0d1322;
  cursor: pointer;
  font-size: 16px;
  width: 200px;
  font-size: 20px;
}
.signinButton:hover {
  background-color: #39e8b0;
  box-shadow: 0 1px 20px #39e8b157;
  transition: 200ms ease;
}

.logologin {
  width: 200px !important;
  object-fit: contain;
}

.login__footer {
  position: fixed;
  bottom: 0px;
  color: rgb(153, 153, 153);
  padding: 20px 20px;
  text-align: center;
}

.typewriter h1 {
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: 1px solid #39e8b0; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto;
  letter-spacing: 0.15em; /* Adjust as needed */
  animation: typing 2.5s steps(40, end), blink-caret 0.75s step-end infinite;
  margin-bottom: 10px;
  animation-delay: 1s;
}

.textFadeIn {
  animation: fade 2s steps(60, end);
  animation-delay: 1s;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #39e8b0;
  }
}

@media screen and (max-width: 768px) {
  .login h1,
  span h2,
  h3,
  h4,
  a {
    font-size: large;
  }
  .login p {
    font-size: small;
  }
  .typeweritter__span {
    font-size: small !important;
  }
  .loginContainer__input {
    font-size: 15px;
  }
  .signinButton {
    font-size: 12px;
  }
}

.splashScreen {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: #111726;
  overflow: hidden;
  /* background-image: url("../../assets/images/noise.png"); */
  background-size: 13%;
  z-index: 100;
  opacity: 1;
  transition: 100ms ease-in;
  display: grid;
  place-items: center;
}

.home {
  color: #c5c7cc;
  height: 100vh;
  padding: 40px 20px;
}

.home > h1 {
  font-size: 30px;
}

.homeContainer__rooms {
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
  padding-bottom: 30px;
}

.home_CreatButton {
  position: fixed;
  bottom: 30px;
  right: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  outline: none;
  background-color: #39e8b0;
  color: #0d1322;
  border: none;
  border-radius: 30px;
  padding: 15px;
  cursor: pointer;
}

.home_CreatButton > h2 {
  font-size: 15px;
  display: none;
}

.home_CreatButton:hover {
  background-color: #39e8b0;
  box-shadow: 0 1px 20px #39e8b157;
  transition: 200ms ease;
}

.homeContainer__roomsEmpty {
  display: grid;
  place-items: center;
  margin-top: 20px;
  color: #c5c7cc79;
}

@media screen and (min-width: 768px) {
  .home_CreatButton {
    right: 30px;
  }
  .home_CreatButton > h2 {
    font-size: 20px;
    display: block;
  }
}

.board {
  border: 1px solid rgba(99, 105, 121, 0.6);
  border-radius: 4px;
  box-shadow: 0 8px 35px rgb(0 0 0 / 40%);
  background-color: #0d1322;
  min-height: 120px;
  padding: 10px 10px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 20px;
  margin-top: 20px;
  width: 100%;
}
.board:hover {
  border: 1px solid #39e8b169;
  box-shadow: 0px 0px 14px #39e8b157;
  -moz-box-shadow: 0px 0px 14px #39e8b157;
  -webkit-box-shadow: 0px 0px 14px #39e8b157;
}

.board > h3 {
  margin-bottom: 5px;
  font-size: 22px;
}
.boardTop {
  display: flex;
  flex-direction: column;
}

.board_Close {
  cursor: pointer;
  border: none;
  outline: none;
  background-color: transparent;
  color: #666666;
}

.board_Close > svg {
  float: right;
}

.board_CloseIcon {
  pointer-events: none;
  outline: none;
  border: none;
}
.board_Close:hover {
  color: white;
  transition: 200ms ease;
}

.boardBotton {
  flex: 1 1;
}

.boardBotton > h3 {
  margin-top: 40px;
}

@media screen and (min-width: 768px) {
  .board {
    max-width: 250px;
  }
}

.createRoomDialog {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  display: grid;
  place-items: center;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background-color: rgba(99, 105, 121, 0.2);
  background-size: 13%;
  background-image: url(/static/media/noise.d9fbba13.png);
  box-shadow: 0 8px 35px rgb(0 0 0 / 40%);
  height: 100vh;
  width: 100vw;
}
.createRoomDialog__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px 20px;
  background-color: #0d1322;
  border-radius: 5px;
  align-items: center;
  color: rgb(148, 148, 148);
  box-shadow: 0 8px 35px rgb(0 0 0 / 40%);
}
.createRoomDialog__container > input {
  border: 1px solid rgba(180, 180, 180, 0.507);
  outline: none;
  background-color: transparent;
  border-radius: 10px;
  padding: 10px 10px;
  color: white;
  margin-bottom: 50px;
  margin-top: 20px;
  font-size: 26px;
  font-weight: 500px;
  width: 100%;
}

.createRoomDialog__container > input:focus {
  outline: none;
  border: 1px solid #39e8b0;
  box-shadow: 0px 0px 14px #39e8b157;
  -moz-box-shadow: 0px 0px 14px #39e8b157;
  -webkit-box-shadow: 0px 0px 14px #39e8b157;
  transition: 200ms ease-in;
}

.createRoomDialog__container > button {
  border: none;
  outline: none;
  background-color: #39e8b0;
  color: #0d1322;
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 50px;
  font-size: 22px;
  width: 100%;
  height: 50px;
  cursor: pointer;
}

.createRoomDialog__container > button:hover {
  background-color: #39e8b0;
  box-shadow: 0 1px 20px #39e8b157;
  transition: 200ms ease;
}

.createRoomDialog__close {
  position: fixed;
  top: 30px;
  right: 20px;
  cursor: pointer;
}

.createRoomDialog__close:hover {
  fill: white !important;
  transition: 200ms ease;
}

@media screen and (min-width: 768px) {
  .createRoomDialog__container {
    min-width: 400px;
  }
}

.room {
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding: 20px 10px;
  color: white;
}

.roomTopContainer {
  flex: 0.1 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.roomTopContainer box-icon {
  cursor: pointer;
}

.roomBottomContainer {
  flex: 0.9 1;
  margin-top: 30px;
  overflow-y: overlay;
  overflow-x: visible;
  min-height: 300px;
  padding-bottom: 30px;
}

.roomTopContainer {
  color: #c5c7cc;
}

.MuiTable-root {
  background-color: #111726;
}

.MuiTableCell-body {
  color: #39e8b1c0 !important;
}

.MuiTableCell-head {
  color: #39e8b0 !important;
}

.MuiTableCell-root {
  border-bottom: 1px solid #aaaaaa5e !important;
  font-family: inherit !important;
}

.MuiPaper-rounded {
  border-radius: 0px !important;
}

.roomTalbeDelete {
  cursor: pointer;
}

.MuiTableCell-body:hover {
  color: #a6fadf !important;
  transition: 200ms ease;
}

.roomBottomContainerNoData {
  margin-bottom: 20px;
  color: #636979;
  text-align: center;
}

.roomMiddleContainer {
  color: #c5c7cc;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  text-align: center;
}
.roomMiddleContainer > p {
  margin-bottom: 20px;
}

.roomMiddleContainerEach {
  display: flex;
  flex-direction: column;
  padding: 10px 20px;
  align-items: center;
}

.roomMiddleContainerAvatars {
  color: #c5c7cc;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 30px;
}
.filesTransferProgress p {
  color: #636979;
}
.MuiAvatar-root {
  background-color: #0d1322 !important;
  width: 60px !important;
  height: 60px !important;
  border: 1px solid #39e8b0;
  box-shadow: 0 1px 20px #39e8b157;
}

.room_sendFile {
  color: transparent;
}
.room_sendFile::-webkit-file-upload-button {
  visibility: hidden;
}
.room_sendFile::-moz-file-upload-button {
  visibility: hidden;
}

.room_sendFile::before {
  content: "Share Files";
  outline: none;
  white-space: nowrap;
  -webkit-user-select: none;
  -moz-user-select: none;
  cursor: pointer;
  font-weight: 700;
  position: fixed;
  bottom: 30px;
  right: 10px;
  font-size: 15px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-color: #39e8b0;
  color: #0d1322;
  border: none;
  border-radius: 30px;
  padding: 15px;
}
.room_sendFile:hover::before {
  background-color: #39e8b0;
  box-shadow: 0 1px 20px #39e8b157;
}
.room_sendFile:active {
  outline: 0;
}

.file-progress svg {
  transform: rotateZ(-90deg) scaleY(-1);
}

.file-progress circle {
  stroke: #3be8b0;
  stroke-width: 2px;
  stroke-dasharray: 69;
  fill: #0b101c;
}

.buttonDisabled::before {
  background-color: #7c7c7c;
}

.buttonDisabled:hover::before {
  background-color: #7c7c7c;
  box-shadow: 0 0px 00px #39e8b157;
}

.roomQrCodeContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.roomQrCodeContainer > p {
  margin-top: 20px;
}

@media screen and (min-width: 768px) {
  .room {
    padding: 20px 20px;
  }
  .room_sendFile::before {
    right: 30px;
  }
  .roomBottomContainer {
    min-height: auto;
    overflow: auto;
  }
}

